import React, { useState } from 'react';
import {
  Button,
  Card,
  Typography,
  Container,
  InputLabel,
  Input,
  IconButton,
  InputAdornment,
  TextField,
  FormControl,
} from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import { Visibility, VisibilityOff } from '@material-ui/icons';
import FacebookIcon from '@material-ui/icons/Facebook';
import { ReactComponent as GoogleIcon } from '../../assets/google_icon.svg';
import { makeStyles } from '@material-ui/core/styles';
import { isMobile } from 'react-device-detect';

import firebase from 'firebase/app';

const useStyles = makeStyles({
  root: {
    height: '100vh',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  card: {
    width: '100%',
    height: isMobile ? '60%' : '50%',
  },
  header: {
    backgroundColor: '#386FC2',
    color: 'white',
    textAlign: 'center',
  },
  logo: {
    padding: '10px 0 10px 0',
  },
  inputsContainer: {
    display: 'flex',
    flexDirection: 'column',
    margin: '25px',
  },
  inputs: {
    marginTop: '15px',
  },
  button: {
    marginTop: '20px',
    color: 'white',
    fontWeight: 'bold',
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[600],
    },
  },
  icon: {
    height: '25px',
    width: '25px',
  },
});

const Login = ({ auth }) => {
  const classes = useStyles();
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const signInWithGoogle = () => {
    const provider = new firebase.auth.GoogleAuthProvider();
    auth.signInWithPopup(provider);
  };

  const signInWithFacebook = () => {
    const provider = new firebase.auth.FacebookAuthProvider();
    auth.signInWithPopup(provider);
  };

  return (
    <Container maxWidth="xs" className={classes.root}>
      <Card className={classes.card}>
        <div className={classes.header}>
          <Typography className={classes.logo} variant="h4">
            InBovis
          </Typography>
        </div>
        <div className={classes.inputsContainer}>
          <TextField disabled className={classes.inputs} label="E-mail" />
          <FormControl disabled className={classes.inputs}>
            <InputLabel htmlFor="password">Senha</InputLabel>
            <Input
              id="password"
              type={showPassword ? 'text' : 'password'}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    onMouseDown={handleMouseDownPassword}
                  >
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              }
            />
          </FormControl>
          <Button className={classes.button} onClick={() => {}}>
            Entrar
          </Button>
          <Button
            className={classes.button}
            onClick={signInWithGoogle}
            endIcon={<GoogleIcon className={classes.icon} />}
          >
            Entrar com o Google
          </Button>
          <Button
            className={classes.button}
            onClick={signInWithFacebook}
            endIcon={
              <FacebookIcon
                style={{ fill: '#386FC2' }}
                className={classes.icon}
              />
            }
          >
            Entrar com o Facebook
          </Button>
        </div>
      </Card>
    </Container>
  );
};

export default Login;
